	module.exports = {"data":{"challengeNode":{"title":"Manipulate Arrays With shift()","description":["<code>pop()</code>函数用来移出数组中最后一个元素。如果想要移出第一个元素要怎么办呢？","这就是<code>.shift()</code>的用武之地。它的工作原理就像<code>.pop()</code>，但它移除的是第一个元素，而不是最后一个。","<hr>","使用<code>.shift()</code>函数移出<code>myArray</code>中的第一项，并把“移出”的值赋给<code>removedFromMyArray</code>。"],"challengeType":1,"fields":{"slug":"/javascript-algorithms-and-data-structures/basic-javascript/manipulate-arrays-with-shift","blockName":"Basic JavaScript","tests":[{"text":"<code>myArray</code>应该等于<code>[[\"dog\", 3]]</code>.","testString":"assert((function(d){if(d[0][0] == 'dog' && d[0][1] === 3 && d[1] == undefined){return true;}else{return false;}})(myArray), '<code>myArray</code>应该等于<code>[[\"dog\", 3]]</code>.');"},{"text":"<code>removedFromMyArray</code>应该包含<code>[\"John\", 23]</code>.","testString":"assert((function(d){if(d[0] == 'John' && d[1] === 23 && typeof removedFromMyArray === 'object'){return true;}else{return false;}})(removedFromMyArray), '<code>removedFromMyArray</code>应该包含<code>[\"John\", 23]</code>.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"// 示例\nvar ourArray = [\"Stimpson\", \"J\", [\"cat\"]];\nvar removedFromOurArray = ourArray.shift();\n// removedFromOurArray now equals \"Stimpson\" and ourArray now equals [\"J\", [\"cat\"]].\n\n// 初始化变量\nvar myArray = [[\"John\", 23], [\"dog\", 3]];\n\n// 请把你的代码写在这条注释以下\nvar removedFromMyArray;\n\n","head":"","tail":"(function(y, z){return 'myArray = ' + JSON.stringify(y) + ' & removedFromMyArray = ' + JSON.stringify(z);})(myArray, removedFromMyArray);"},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/javascript-algorithms-and-data-structures/basic-javascript/manipulate-arrays-with-unshift","id":"56bbb991ad1ed5201cd392cd"},"slug":"/javascript-algorithms-and-data-structures/basic-javascript/manipulate-arrays-with-shift"}}

